import './css/section_01.css'
import './css/careers.css'
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';
import Careers_01 from './Careers_01.js'
import Careers_02 from './Careers_02.js'
import Careers_03 from './Careers_03.js'

function CareerWrapper(){
    let navigate = useNavigate();//링크연결
    return(
        <> 
        <Routes>
            <Route path="/" element={<Careers navigate={navigate}/>}>
                <Route path="/" element={<Careers_01/>}/>   
                <Route path="/d" element={<Careers_02/>}/>
                <Route path="f" element={<Careers_03/>}/>
            </Route>
        </Routes>  
        </>
    )
}
function Careers(){ 
    return(
        <>
        <section class="p_l_d_b">
            <div class="careers_visual"></div> 
            <div class="section_01_wrapper">
                <Outlet/>
            </div>
        </section>
        </>
    )
}
export default CareerWrapper;
